.newsContainer{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    gap: 40px;
    padding: 40px 100px;
    background-color: #eeeeee;
}
.heading{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80px;
    margin: 50px 0 0 0;
    background-color: #eeeeee;
}
.heading h2{
    border-bottom: 5px solid #FF2626;
    padding: 5px 0;
    width: 100%;
    font-size: 3.5rem;
    text-align: center; 
    color: #FF2626;
     background-color: white;
}
.footerButtons{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    box-shadow: 0px 2px 14px 1px rgb(0 0 0 / 38%);
    -webkit-box-shadow: 0px 2px 14px 1px rgb(0 0 0 / 38%);
    -moz-box-shadow: 0px 2px 14px 1px rgba(0,0,0,0.38);
}
.footerButtons button{
    text-decoration: none;
    padding: 8px 20px;
    font-size: 16px;
    color: #F5F5F5;
    background-color: #1A374D;
    border: none;
    outline: none;
    border-radius: 5px;
    transition: 0.4s ease-in-out;
}
.footerButtons button:hover{
    cursor: pointer;
    background-color: #FF2626;
}
.footerButtons button:disabled,
.footerButtons button[disabled]{
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
}
@media (max-width:1220px){
    .newsContainer{
        grid-template-columns: repeat(3,1fr);
        gap: 25px;
        padding: 20px 70px;
    }
}

@media(min-width:768px) and (max-width:980px){
    .newsContainer{
        grid-template-columns: repeat(2,1fr);
        gap: 40px;
        padding: 20px 60px;
    }
}

@media(max-width:360px){
    .newsContainer{
        grid-template-columns: repeat(1,1fr);
        gap: 30px;
        padding: 20px;
    }
}
@media(min-width:360px) and (max-width:767px){
    .newsContainer{
        grid-template-columns: repeat(1, 1fr);
        gap: 30px;
    }
}
@media(min-width:1440px){
    .newsContainer{
        grid-template-columns: repeat(4,1fr);
    }
}
@media(min-width:1600px){
    .newsContainer{
        grid-template-columns: repeat(5,1fr);
    }
}